<template>
  <div class="pt88">
    <head-bar v-bind:head="headdata"></head-bar>
    <div class="heados">
      <div class="register-cont">
        <div class="register-logo">
          <img :src="logo">
        </div>
        <div class="register-btn register-phone" @click="changeType(false)" v-if="type">使用手机号码注册</div>
        <div class="register-btn register-phone" @click="changeType(true)" v-else>使用Email注册</div>
        <div class="register-cut">————<span>or</span>————</div>
        <div class="register-form">
          <div class="form-item" v-if="type">
            <div class="form-label">Email</div>
            <div class="form-input">
              <input type="text" v-model="formData.email" placeholder="请输入你的Email"/>
            </div>
          </div>
          <div class="form-item" v-else>
            <div class="form-label">手机号码</div>
            <div class="form-input">
              <input type="text" v-model="formData.phone" placeholder="请输入你的手机号码"/>
            </div>
          </div>
          <div class="form-item">
            <div class="form-label">密码</div>
            <div class="form-input">
              <input type="password" v-model="formData.pwd" placeholder="请输入你的密码"/>
            </div>
          </div>
          <div class="form-item">
            <div class="form-label">确认密码</div>
            <div class="form-input">
              <input type="password" v-model="formData.cfPwd" placeholder="请再次输入密码"/>
            </div>
          </div>
          <div class="register-btn gradient-back" @click="toResit()">注册</div>
        </div>
        <div class="toLogin">已有账号？ <router-link :to="{name:'login', params:{status: true}}"> 登录</router-link></div>
      </div>
    </div>
  </div>
</template>

<script>
import headBar from '@/components/common/head';

export default {
  components: {
    headBar
  },
  name: 'register',
  data () {
    return {
      msg: 'register',
      headdata: {
        text: '注册'
      },
      logo: require('@/assets/images/logo.png'),
      type: true,
      formData: {
        email: '',
        phone: '',
        pwd: '',
        cfPwd: '',
      }
    }
  },
  created(){
    this.initPage();
  },
  methods: {
    initPage(){
      const type = this.$route.params.type;
      // 路由参数修改注册方式
      if(type == 'phone'){
        this.changeType(false);
      }
      if(!this.DB.db){
        this.DB.init();
      }
    },
    // 切换注册方式
    changeType(status){
      this.type = status;
      // 重置表单
      for(let key in this.formData){
        this.formData[key] = '';
      }
    },
    // 表单验证
    toResit(){
      var msg = '';
      var regPhone = /^1(3|4|5|7|8)\d{9}$/;
      var regEmail = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/;
      if(this.type && this.formData.email.length==0){
        msg = '请输入你的Email';
      }else if(this.type && this.formData.email.length>0 && !regEmail.test(this.formData.email)){
        msg = '请输入正确的Email';
      }else if(!this.type && this.formData.phone.length==0){
        msg = '请输入你的手机号码';
      }else if(!this.type && this.formData.phone.length>0 && !regPhone.test(this.formData.phone)){
        msg = '请输入正确的手机号码';
      }else if(this.formData.pwd.length==0){
        msg = '请输入你的密码';
      }else if(this.formData.cfPwd.length==0){
        msg = '请再次输入密码';
      }else if(this.formData.cfPwd !== this.formData.pwd){
        msg = '两次输入密码不同';
      }
      if(msg.length>0){
        this.$toast(msg)
      }else{
        this.saveLog();
      }
    },
    // 注册请求
    saveLog(){
      var _this = this;
      // 参数准备
      var data = {
        title: 'register',
        email: _this.formData.email,
        password: _this.formData.pwd,
        phone: _this.formData.phone,
        infoStatus: 0 // 完善信息状态 0 未完善 1 已完善
      }
      //添加用户信息
      _this.DB.add(data, function(res){
        _this.$toast('注册成功');
        setTimeout(()=>{
          _this.$router.push({
            path: '/login',
            name: 'login'
          });
        },1500)
      },function(res){
        setTimeout(()=>{
          _this.$router.push({
            path: '/login',
            name: 'login'
          });
        },1500)
        _this.$toast('无法注册')
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.register-cont{
  padding: 0 0.5rem;
}
.register-logo{
  width: 2.1rem;
  height: 2.1rem;
  margin: 0.5rem auto;
}
.register-logo img{
  box-shadow: 0 0 0.5rem 0 rgba(221,221,221,0.5);
  border-radius: 0.5rem;
}
.register-btn{
  width: 100%;
  height: 1rem;
  line-height: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 0.1rem 0.2rem 0 #ddd;
}
.register-phone{
  background: #3b5998;
  color: #fff;
}
.register-cut{
  padding: 0.3rem 0;
  color: #e9e9e9;
}
.register-cut span{
  color: #282c37;
  padding: 0 0.3rem;
}
.register-form{
  overflow: hidden;
}
.form-item{
  text-align: left;
  border: 1px solid #e9e9e9;
  border-radius: 0.1rem;
  margin-bottom: 0.32rem;
  padding: 0.2rem 0.3rem;
  position: relative;
}
.form-item:last-child{
  margin-bottom: 0;
}
.form-label{
  font-size: 0.26rem;
  font-weight: bold;
  color: #5856d6;
  margin-bottom: 0.1rem;
}
.form-input input{
  width:100%;
  height: 0.4rem;
  line-height: 0.4rem;
  color: #282c37;
  font-size: 0.36rem;
}
.toLogin{
  margin-top: 0.6rem;
  font-size: 0.3rem;
}
.toLogin a{
  color: #ff5e3a;
}
</style>



